<template>
    <div class="errortest">
        <div class="Errortesttitle">
        <Errortesttitle @goStatefn='goState'></Errortesttitle>
        </div>
        <div class="content">
            <router-view :DriveanswerData="DriveanswerData"></router-view>
        </div>
    </div>
</template>

<style scope lang="less">
.errortest{
.Errortesttitle{
     background: white;
}
}

</style>
<script>
//导入头部
import Errortesttitle from './Errortesttitle'
//导入json文件
import driveanswer from '../../../public/driveanswer.json'

export default {
    name:'errortest',
    components:{
    Errortesttitle
    },
    data() {
        return {
            //题目数据
            DriveanswerData:driveanswer,
        }
    },
    methods: {
        goState(path){
            this.$router.push(path)
        }
    },
}
</script>